
#menu{
	width: 100%;
	height: 50px;
	margin: 0px;
	padding: 0px;
	background: #545252;
}
.el-menu{
	width:70%;
	height: 50px;
	margin:0px auto;
	display: block;
	line-height: 50px;
	background: #545252;
	color:white;
}
.el-menu-item{
	width:100px;
	height: 50px;
	color:white;
	display: inline-block;
	line-height: 50px;
	padding: 0px;
	margin:0px;	
	text-align: left;
	border-right:1px solid #070707 ;
}
.el-menu-item:first-child{
	border-left: 1px solid #070707 ;
}
.el-menu-item:hover{
	background: #837E7E
}




